import store from "../store";
import { ethers } from "ethers";

const mixins = {
  computed: {
    balance() {
      return store.state.auth.balance;
    },
    walletAddress() {
      return store.getters.walletAddress;
    },
    ethereumAddress() {
      return store.getters.ethereumAddress;
    },
    id_card() {
      return store.getters.id_card;
    },
    verifyToken() {
      return store.getters.verifyToken;
    },
  },
  methods: {
    parseUtillETH(val) {
      const value = ethers.BigNumber.from(String(val));
      return ethers.utils.formatEther(value);
    },
    app_loading(state) {
      store.commit("SET_APP_LOADING", state);
    },
    alert_show(data) {
      return new Promise((resolve, reject) => {
        store.commit("SET_APP_ALERT_SHOW", [data, resolve, reject]);
      });
    },
    alert_close() {
      store.commit("SET_APP_ALERT_CLOSE");
    },
    DateTime(stmpDate) {
      let d = this.$moment(stmpDate);
      var month = new Array();
      month[0] = "";
      month[1] = "";
      month[2] = "";
      month[3] = "";
      month[4] = "";
      month[5] = "";
      month[6] = "";
      month[7] = "";
      month[8] = "";
      month[9] = "";
      month[10] = "";
      month[11] = "";
      let year = parseInt(d.year()) + 543;
      return (
        this.twoDigit(d.date()) +
        " " +
        month[d.month()] +
        " " +
        year +
        " " +
        this.twoDigit(d.hour()) +
        ":" +
        this.twoDigit(d.minute()) +
        " ."
      );
    },
    DateOnly(stmpDate) {
      let d = this.$moment(stmpDate);
      var month = new Array();
      month[0] = "";
      month[1] = "";
      month[2] = "";
      month[3] = "";
      month[4] = "";
      month[5] = "";
      month[6] = "";
      month[7] = "";
      month[8] = "";
      month[9] = "";
      month[10] = "";
      month[11] = "";
      let year = parseInt(d.year()) + 543;
      return this.twoDigit(d.date()) + " " + month[d.month()] + " " + year;
    },
    DateOne(stmpDate) {
      try {
        var month = new Array();
        month[0] = "";
        month[1] = "";
        month[2] = "";
        month[3] = "";
        month[4] = "";
        month[5] = "";
        month[6] = "";
        month[7] = "";
        month[8] = "";
        month[9] = "";
        month[10] = "";
        month[11] = "";
        let res = stmpDate.match(/-/);
        if (res == null) {
          let year = stmpDate.substr(0, 4);
          let m = parseInt(stmpDate.substr(4, 2)) - 1;
          let day = stmpDate.substr(6, 2);
          return day + " " + month[m] + " " + year;
        } else {
          let d = this.$moment(stmpDate);
          let year = parseInt(d.year()) + 543;
          return this.twoDigit(d.date()) + " " + month[d.month()] + " " + year;
        }
      } catch (err) {
        return "";
      }
    },
    twoDigit(digit) {
      if (String(digit).length == 1) {
        return "0" + digit;
      } else {
        return digit;
      }
    },
    focusInput(e) {
      window.scroll({
        top: e.target.offsetTop,
        behavior: "smooth",
      });
    },
    diffDate(x, y) {
      var x = new this.$moment(x);
      var y = new this.$moment(y);
      var duration = this.$moment.duration(y.diff(x));
      return duration.asDays();
    },
    formatMoney(amount, decimalCount = 2, decimal = ".", thousands = ",") {
      try {
        decimalCount = Math.abs(decimalCount);
        decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

        const negativeSign = amount < 0 ? "-" : "";

        let i = parseInt(
          (amount = Math.abs(Number(amount) || 0).toFixed(decimalCount))
        ).toString();
        let j = i.length > 3 ? i.length % 3 : 0;

        return (
          negativeSign +
          (j ? i.substr(0, j) + thousands : "") +
          i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) +
          (decimalCount
            ? decimal +
              Math.abs(amount - i)
                .toFixed(decimalCount)
                .slice(2)
            : "")
        );
      } catch (e) {
        console.log(e);
      }
    },
  },
};

export default mixins;
